.homepage {
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    background-image: url('../images/bg.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;

    width: 100%;
    height: 100vh;
    padding: 50px;
}

.out {
    top: 100%;
}

.homepage .master {
    width: 100%;
    display: grid;
    justify-items: center;
}

#wdev {
    position: relative;
    top: 50%;
    width: 65%;
    fill: transparent;
    stroke: black;
    stroke-width: 3px;
    animation: fill 0.5s linear forwards 2.2s, movement 1s cubic-bezier(0.075, 0.82, 0.165, 1) forwards 3s;
}

.homepage .passion {
    clip-path: circle(0 at 0 0);
    animation: appear 1s ease-in-out forwards 3s;
}

.homepage .info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.homepage .info .header {
    font-size: 16pt;
}

.homepage .links {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    flex-wrap: wrap;
    margin-top: 25px;
    width: 100%;
}

.homepage .links * {
    color: white;
    font-size: 15pt;
    display: flex;
    align-items: center;
    margin: 5px;
}

#right-arrow, #left-arrow {
    width: 20px;
    fill: white;
}

#path3764, #path3780{
    stroke-dasharray: 238;
    stroke-dashoffset: 238;
    animation: contour 3s ease-in-out forwards;
}
#path3766{
    stroke-dasharray: 585;
    stroke-dashoffset: 585;
    animation: contour 3s ease-in-out forwards;
}
#path3768, #path3774{
    stroke-dasharray: 404;
    stroke-dashoffset: 404;
    animation: contour 3s ease-in-out forwards;
}
#path3770{
    stroke-dasharray: 444;
    stroke-dashoffset: 444;
    animation: contour 3s ease-in-out forwards;
}
#path3772{
    stroke-dasharray: 468;
    stroke-dashoffset: 468;
    animation: contour 3s ease-in-out forwards;
}
#path3776{
    stroke-dasharray: 308;
    stroke-dashoffset: 308;
    animation: contour 3s ease-in-out forwards;
}
#path3778{
    stroke-dasharray: 264;
    stroke-dashoffset: 264;
    animation: contour 3s ease-in-out forwards;
}

@keyframes contour {
    to {
        stroke-dashoffset: 0;
        stroke-width: 1;
    }
}
@keyframes fill {
    from {
        fill: transparent;
    }
    to {
        fill: black;
    }
}
@keyframes movement {
    from {
        top: 50%;
    }
    to {
        top: 0;
    }
}
@keyframes appear {
    from {
        clip-path: circle(0 at 0 50%);
    }
    to {
        clip-path: circle(140% at 0 50%);
    }
}
@media only screen and (max-width: 500px) {
    #wdev {
        width: 85%;
    }
    .homepage {
        padding: 10px;
    }
}