.focus-enter {
    top: 100vh;
}

.focus-enter-done {
    top: 0;
}

.focus-exit {
    top: 0;
}

.focus-exit-active {
    top: 100vh;
}

.focus-enter-done, .focus-exit-active {
    transition-property: top;
    transition-duration: 0.5s;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}