.error-page {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;

    background-color: rgb(237, 237, 237);
    min-height: 100vh;
    width: 100%;

    position: absolute;
    top: 0;
    left: 0;
    padding: 0 20px;
    padding-top: 55px;
}

.error-page .lost {
    height: 40vh;
    margin: 30px 0;
}

.error-page .smol-text {
    font-size: 15pt;
    word-wrap: normal;
    text-align: center;
    margin: 10px 5px;
}

.error-page .error {
    color: black;
    position: absolute;
    bottom: 0;
}

@media only screen and (max-width: 550px) {
    .error-page {
        justify-content: center;
        padding-top: 0;
    }
    .error-page .lost {
        width: 70vw;
        height: auto;
    }
    .error-page .error {
        font-size: 18pt;
    }
}